@import "../../common/color.scss";

.movie-list {
    margin : 20px 0px;
    h2 {
        color:  $font-secondery;
        margin-bottom: 10px;
        font-weight: 400;
    }
}

.movie-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-gap: 15px;
}